<template>
	<view class="box">
		<view class="search-box">
			<navigator url='/pages/index/good_search'>
				<view class="left">
					<view class="iconfont iconsousuo1"></view>
					<input class="serarch" disabled type="text" placeholder="请输入关键字">
				</view>
			</navigator>
			<navigator url='/pages/member/message_center' class="iconfont iconshouye-xiaoxi">
				<!-- <text class="dot"></text> -->
			</navigator>
		</view>
		<!-- 此处要做banner -->
		<area-banner :type="'index'"></area-banner>
		<view class="special-area no-special-area scroll-area" v-if="!type_check">
			<navigator url='/pages/index/area_new_people' class="special-li">
				<view class="iconfont iconxinrenzhuanqu1 bg-blue"></view>
				<view class="text">新人购</view>
			</navigator>
			<navigator url='/pages/index/area_group_wholesale' class="special-li">
				<view class="iconfont icontuanpizhuanqu bg-pink"></view>
				<view class="text">团批专区</view>
			</navigator>
			<navigator url='/pages/index/area_activity' class="special-li">
				<view class="iconfont iconhuodongzhuanqu bg-purple"></view>
				<view class="text">活动专区</view>
			</navigator>
		</view>

		<!-- 专区 -->
		<scroll-view v-if="type_check" id="tab-bar" class="scroll-h scroll-area" :scroll-x="true" :show-scrollbar="false">
			<view class="special-area uni-tab-item">
				<navigator url='/pages/index/area_new_people' class="special-li uni-tab-item-title">
					<view class="iconfont iconxinrenzhuanqu1 bg-blue"></view>
					<view class="text">新人购</view>
				</navigator>
				<navigator url='/pages/index/crowd_funding' class="special-li uni-tab-item-title">
					<view class="iconfont iconzhongchouzhuanqu bg-pink"></view>
					<view class="text">众筹专区</view>
				</navigator>
				<navigator url='/pages/index/heath_product' class="special-li uni-tab-item-title">
					<view class="iconfont iconjiankangzhuanqu bg-green"></view>
					<view class="text">健康专区</view>
				</navigator>
				<navigator url='/pages/index/real_shop' class="special-li uni-tab-item-title">
					<view class="iconfont iconshitidian bg-purple"></view>
					<view class="text">实体店</view>
				</navigator>
				<view class="special-li uni-tab-item-title">
					<view class="iconfont iconqita purple-orange "></view>
					<view class="text">其他</view>
				</view>
			</view>
		</scroll-view>

		<!-- 分类商品 -->
		<view class="good-classified">
			<view class="type-list">
				<view class="left">
					<scroll-view id="tab-bar" class="scroll-h" :scroll-x="true" :show-scrollbar="false">
						<view v-for="(tab,index) in cate_list" :key="index" class="type-li uni-tab-item" :id="index" :data-current="index"
						 @click="chooseCate(tab.catCode)">
							<text class="uni-tab-item-title" :class="tab.catCode==choose_cate ? 'active uni-tab-item-title-active' : ''">{{tab.catName}}</text>
						</view>
					</scroll-view>
				</view>
				<navigator url="/pages/index/goods" class="right">
					全部
					<view class="iconfont iconxiayibu"></view>
				</navigator>
			</view>

			<!-- 商品列表 -->
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
				<view class="goods">
					<view class="goods-box">
						<template v-for="(item,k) in data_list">
							<navigator class="goods-li" :url="'/pages/goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId"
							 :key="k">
								<image class="image-wrapper img" :class="{'loaded':item.loaded}" @load="onImageLoad('data_list',k)" :src="item.mainPicUrl" mode="aspectFill"></image>
								<view class="title-box">
									<view class="tips-box" v-if="data_list[k].spuLabel">
										<template v-for="(val,i) in data_list[k].spuLabel">
											<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
												{{val}}
											</view>
										</template>
									</view>
									<view class="title">
										{{item.spuTitle}}
									</view>
									<view class="price-box">
										<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
									</view>
								</view>
							</navigator>
						</template>
					</view>
				</view>
			</mescroll-body>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import areaBanner from '@/components/area-banner.vue'
	import Vue from 'vue'
	import {
		mapState
	} from 'vuex';
	let page_index = 0;
	let cut=null;
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				upOption: {
					page: {
						size: 6
					},
				},
				data_list: [],
				cate_list: [{
					catCode: "-1",
					catLevel: 1,
					catName: "全部",
					createBy: "",
					createDate: "",
					merchantSeqId: "",
					parentCode: "0",
					picUrl: "",
					remarks: "",
					seqId: "",
					sort: 1,
					status: "SHOW",
					typeValue: null,
					updateBy: "",
					updateDate: "",
				}],
				choose_cate: -1,
			}
		},
		components: {
			areaBanner,
			MescrollBody,
		},
		computed: {
			...mapState({
				type_check: state => state.type_check,
			})
		},
		onHide() {
			clearTimeout(cut);
			cut=null
		},
		methods: {
			onImageLoad(item,k){
				this.$set(this[item][k], 'loaded', true);
			},
			getData() {
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0)
				this.getCate();
				this.getCheck();
				let self=this;
				// 确保刷新时候图片能显示
				cut=setTimeout(function(){
					let len=self.data_list.length;
					let a=0
					if(len>0){
						a=len>6?6:len;
						for(let i=0;i<a;i++){
							let the_data=self.data_list[i];
							self.$set(the_data, 'loaded', true);
						}
					}
				},2000)
				
			},
			getCheck() {
				this.$api.get('/O2oMenuConfig/selectO2oMenu').then(res => {
					if (res.code == 200) {
						let type_check = res.data.menu == 1 ? true : false;
						this.$store.commit('saveTypecheck', type_check)
					}
				})
			},
			chooseCate(id) {
				this.choose_cate = id;
				this.data_list = [];
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0)
			},
			// 获取类别
			getCate() {
				this.$api.get('/o2oGoodsCategory/findCategoryLevel1').then((res => {
					if (res.code == 200) {
						let data = res.data;
						this.cate_list = this.cate_list.concat(data)
					}
				}))
			},
			// 下拉请求
			loadNewData() {
				// 没有数据就不再请求
				if (this.data_list.length == 0 && page_index > 0) return;
				this.mescroll.triggerUpScroll();
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				page_index = pageNum;
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pCatCode: this.choose_cate == '-1' ? "" : this.choose_cate,
						pageno: pageNum,
						pagesize: pageSize,
						promptType: 'NONE', //促销类型
						purchaseType: 'ZC', //购买类型
						onSale: 'SALE'
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for (let val of curPageData) {
						if (val.spuLabel) {
							val.spuLabel = val.spuLabel.split(',')
						}
						this.$set(val,'loaded',false)
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
		}
	}
</script>

